import * as React from 'react';

import Container from '../components/gatsby/Container';
import Page from '../components/gatsby/Page';

interface PageTemplateProps {
    data: {
        site: {
            siteMetadata: {
                title: string;
                description: string;
                author: {
                    name: string;
                    url: string;
                };
            };
        };
        markdownRemark: {
            html: string;
            excerpt: string;
            frontmatter: {
                title: string;
            };
        };
    };
}

export default class PageTemplate extends React.PureComponent<PageTemplateProps> {
    public render() {
        const {data} = this.props;
        return (
            <Page>
                <Container>
                    <h1>{data.markdownRemark.frontmatter.title}</h1>
                    <div dangerouslySetInnerHTML={{__html: data.markdownRemark.html}} />
                </Container>
            </Page>
        );
    }
}

export const query = graphql`
    query PageTemplateQuery($slug: String!) {
        site {
            siteMetadata {
                title
                description
                author {
                    name
                    url
                }
            }
        }
        markdownRemark(fields: {slug: {eq: $slug}}) {
            html
            excerpt
            frontmatter {
                title
            }
        }
    }
`;



